namespace $ { export class $mpk_tss_reports_axle_chart_pane extends $mol_plot_pane {

	/// minimal_width 800
	minimal_width() {
		return 800
	}

	/// minimal_height 300
	minimal_height() {
		return 300
	}

	/// gap_hor 48
	gap_hor() {
		return 48
	}

	/// gap_vert 24
	gap_vert() {
		return 24
	}

	/// hue_base 140
	hue_base() {
		return 140
	}

	/// hue_shift 111
	hue_shift() {
		return 111
	}

	/// scale_x?val 0
	@ $mol_mem
	scale_x( val? : any , force? : $mol_atom_force ) {
		return ( val !== void 0 ) ? val : 0
	}

	/// plugins / <= Touch
	plugins() {
		return [].concat( this.Touch() ) as readonly ( any )[]
	}

	/// Touch $mol_touch
	/// 	zoom?val <=> scale_x?val
	/// 	pan?val <=> shift?val
	@ $mol_mem
	Touch() {
		return (( obj )=>{
			obj.zoom = ( val? : any ) => this.scale_x( val )
			obj.pan = ( val? : any ) => this.shift( val )
			return obj
		})( new this.$.$mol_touch )
	}

} }

