namespace $ { export class $mpk_tss extends $mol_view {

	/// title \Terminal № %terminal_number | Traffic security system
	title() {
		return "Terminal № %terminal_number | Traffic security system"
	}

	/// attr * mol_theme <= theme
	attr() {
		return ({
			"mol_theme" :  this.theme() ,
		})
	}

	/// theme \$mol_theme_light
	theme() {
		return "$mol_theme_light"
	}

	/// terminal_text @ \Terminal № %terminal_number
	terminal_text() {
		return this.$.$mol_locale.text( "$mpk_tss_terminal_text" )
	}

	/// sub /
	/// 	<= Head
	/// 	<= Body
	sub() {
		return [].concat( this.Head() , this.Body() ) as readonly ( any )[]
	}

	/// Head $mol_view sub /
	/// 	<= Terminal_link
	/// 	<= Logged_user
	@ $mol_mem
	Head() {
		return (( obj )=>{
			obj.sub = () => [].concat( this.Terminal_link() , this.Logged_user() ) as readonly ( any )[]
			return obj
		})( new this.$.$mol_view )
	}

	/// Terminal_link $mol_button_minor
	/// 	enabled false
	/// 	sub / <= terminal_formatted_label
	@ $mol_mem
	Terminal_link() {
		return (( obj )=>{
			obj.enabled = () => false
			obj.sub = () => [].concat( this.terminal_formatted_label() ) as readonly ( any )[]
			return obj
		})( new this.$.$mol_button_minor )
	}

	/// terminal_formatted_label \
	terminal_formatted_label() {
		return ""
	}

	Logged_user() {
		return this.Body().Logged_user(  )
	}

	/// Body $mpk_tss_main Logged_user => Logged_user
	@ $mol_mem
	Body() {
		return (( obj )=>{
			return obj
		})( new this.$.$mpk_tss_main )
	}

} }

