namespace $ { export class $mpk_tss_reports_resolution extends $mol_view {

	/// attr *
	/// 	^
	/// 	mpk_tss_reports_resolution_type <= status
	attr() {
		return ({
			...super.attr() ,
			"mpk_tss_reports_resolution_type" :  this.status() ,
		})
	}

	/// status \
	status() {
		return ""
	}

	/// statuses *
	/// 	success <= success
	/// 	warning <= warning
	statuses() {
		return ({
			"success" :  this.success() ,
			"warning" :  this.warning() ,
		})
	}

	/// success @ \Success
	success() {
		return this.$.$mol_locale.text( "$mpk_tss_reports_resolution_success" )
	}

	/// warning @ \Warning
	warning() {
		return this.$.$mol_locale.text( "$mpk_tss_reports_resolution_warning" )
	}

	/// icons *
	/// 	success <= Success
	/// 	warning <= Warning
	icons() {
		return ({
			"success" :  this.Success() ,
			"warning" :  this.Warning() ,
		})
	}

	/// Success $mpk_tss_icon_circle_outline
	@ $mol_mem
	Success() {
		return (( obj )=>{
			return obj
		})( new this.$.$mpk_tss_icon_circle_outline )
	}

	/// Warning $mpk_tss_icon_round_warning
	@ $mol_mem
	Warning() {
		return (( obj )=>{
			return obj
		})( new this.$.$mpk_tss_icon_round_warning )
	}

	/// sub / <= Text
	sub() {
		return [].concat( this.Text() ) as readonly ( any )[]
	}

	/// Text $mol_view sub / <= status_text
	@ $mol_mem
	Text() {
		return (( obj )=>{
			obj.sub = () => [].concat( this.status_text() ) as readonly ( any )[]
			return obj
		})( new this.$.$mol_view )
	}

	/// status_text \
	status_text() {
		return ""
	}

} }

